._button_104kb_1._twitter_104kb_1 {
		background-image: url('img/twitter.png');
		background-color: blue; width: 3rem; 
	}
._button_104kb_1._youtube_104kb_1 {
		background-image: url('img/youtube.png');
		background-color: red; width: 4rem; 
	}
._blue_button_104kb_1, ._red_button_104kb_1 { 
	border-radius: 0.5rem; 
	padding: 1em; 
	border-width: 0.0625rem; 
	border-style: solid; 
}
._blue_button_104kb_1 { 
	border-color: #2F74D1; 
	background-color: #3B8EFF; 
}
._red_button_104kb_1 { 
	border-color: #C41A1E; 
	background-color: #FF2025;
}
/*测试cssnext*/
._cssnext_104kb_1{
  color: rgba(255, 0, 0, 0.9);
}
/*测试autoprefixer*/
._autoprefixer_104kb_1{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
}
/*测试Precss*/
._precss_104kb_1 {
    color: #666; /* 变量测试 */
		font-size: 16px;
}
/* 条件 */
._precss_104kb_1 p:nth-of-type(1) { 
			margin-left: calc( 100% / 1 ); 
		}
._precss_104kb_1 p:nth-of-type(2) { 
			margin-left: calc( 100% / 2 ); 
		}
._precss_104kb_1 p:nth-of-type(3) { 
			margin-left: calc( 100% / 3 ); 
		}
._precss_104kb_1 ._icon-twitter_104kb_1 { 
			background: url('img/twitter.png'); 
		}
._precss_104kb_1 ._icon-facebook_104kb_1 { 
			background: url('img/facebook.png'); 
		}
._precss_104kb_1 ._icon-youtube_104kb_1 { 
			background: url('img/youtube.png'); 
		}
/* 循环:@for和@each */
._precss_104kb_1 ._child1_104kb_1 {
        color: green;
    }
._precss_104kb_1._active_104kb_1 {
		font-weight: bold; 
}
._precss_104kb_1:hover {
		font-size: 18px; 
}

